#ModalSplashScreen {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.7);
    z-index: 2;
    display: grid;
    -ms-flex-align: center;
        align-items: center;
    justify-items: center;
}

#ModalSplashScreen .ModalDisplay {
    background: white;
    width: 600px;
    padding:40px;
    border-radius:20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.ModalDisplay .exit-btn {
    float: right;
}

.ModalDisplay .action-btn {
    display: block;
    margin: 0 auto;
    background-color: steelBlue;
    color: white;
    height: 40px;
    border:none;
    font-weight:600;
    border-radius:10px;
}

@media only screen and (max-width:600px) {
    #ModalSplashScreen .ModalDisplay {
        width: 100%;
        padding:40px;
        border-radius:20px;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
    }
}



